/* src/pages/Analysis.css */

.analysis-container {
  padding: 2rem;
}

.analysis-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 2rem;
}

.analysis-table th, .analysis-table td {
  padding: 1rem;
  border: 1px solid #ddd;
  text-align: center;
  font-size: 0.9rem;
}

.analysis-table th {
  background-color: #333;
  color: #fff;
}

.analysis-table td {
  color: #555;
}
