/* src/pages/Home.css */

.home-container {
  padding-top: 5rem;
  text-align: center;
}

.home-header {
  font-size: 2rem;
  color: #333;
  margin: 2rem 0;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  padding: 2rem;
}

.card {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 1rem;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  text-align: left;
}

.card h3 {
  color: #222;
  margin-bottom: 0.5rem;
}

.card p {
  color: #555;
  font-size: 0.9rem;
}
